 header{
  background-color: black;
  color:white;
  padding:20px;
  text-align:center;


}
.score{
    height:100px;
    width:100px;
    display:flex;
    align-items: center;
    justify-content: center;
    background-color: yellow;
    position:relative;
    left:85vw;
    flex-direction: column;
    gap:20px;
}
.revealer{
  height:80px;
  width:80px;
  background-color: green;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
}
main{
  display:flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap:20px;
  text-align: center;
}
input{
  padding:10px;
  background-color: rgb(red, green, blue);
  width:40px;
  border-radius:3px;
  color:brown;
}
button{
  padding:10px;
  background-color: black;
  border-radius:3px;
  color:white;
}
.suggestionbox{
  padding:10px;
  background-color: green;
  border-radius:3px;
  color:white;
} 





